$black: #000;
$primary: #f3a000;

.search-hover-container {
  height: 100vh;
  width: 100vw;
  position: absolute;
  background: rgba($color: $black, $alpha: 0.85);
  top: 100%;
  left: 0%;
  z-index: 50;
  padding: 50px 20px;
}
.button-hover:hover {
  color: $primary;
  cursor: pointer;
}
